interface AIResponse {
  choices: Array<{
    message: {
      content: string
    }
  }>
}

export async function fetchPersonalizedPlan(prompt: string): Promise<string> {
  try {
    const apiKey = process.env.NEXT_PUBLIC_AI_API_KEY || 'AIzaSyBMaH6Ci5e4TvsyGwnXatU9zK0r4Rt5iSU'
    
    const response = await fetch('https://openrouter.ai/api/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${apiKey}`,
        'Content-Type': 'application/json',
        'HTTP-Referer': typeof window !== 'undefined' ? window.location.origin : 'http://localhost:8000',
        'X-Title': 'Perform AI'
      },
      body: JSON.stringify({
        model: 'openai/gpt-4o',
        messages: [
          {
            role: 'system',
            content: `Tu es un entra√Æneur de football professionnel sp√©cialis√© dans l'accompagnement des jeunes de 13-19 ans. 
            Tu donnes des conseils personnalis√©s sur l'entra√Ænement, la nutrition et la pr√©paration mentale. 
            Tes r√©ponses sont motivantes, pr√©cises et adapt√©es √† l'√¢ge. 
            Tu utilises un ton encourageant et professionnel.
            Limite tes r√©ponses √† 150 mots maximum.`
          },
          {
            role: 'user',
            content: prompt
          }
        ],
        max_tokens: 200,
        temperature: 0.7
      })
    })

    if (!response.ok) {
      throw new Error(`API Error: ${response.status}`)
    }

    const data: AIResponse = await response.json()
    
    if (data.choices && data.choices.length > 0) {
      return data.choices[0].message.content.trim()
    } else {
      throw new Error('No response from AI')
    }
  } catch (error) {
    console.error('AI API Error:', error)
    return getFallbackAdvice()
  }
}

