"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Separator } from '@/components/ui/separator'
import { Badge } from '@/components/ui/badge'
import { getRandomExercises } from '@/lib/exercises'
import { fetchNutritionAdvice, fetchTrainingTip } from '@/lib/ai'
import type { Exercise } from '@/lib/exercises'

const days = ['Dimanche', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi']
const programs = [
  'Circuit Technique + Pasta Party Recipe',
  'Cardio Intense + Smoothie Prot√©in√©',
  'Renforcement + Salade √ânerg√©tique',
  'Technique Ballon + Wrap Recovery',
  'HIIT + Bowl Antioxydant',
  'Tactique + Quinoa Power',
  'R√©cup√©ration Active + Hydratation+'
]

export default function DailyTrainingPage() {
  const [todayExercises, setTodayExercises] = useState<Exercise[]>([])
  const [nutritionTip, setNutritionTip] = useState<string>('')
  const [trainingTip, setTrainingTip] = useState<string>('')
  const [loading, setLoading] = useState(true)
  const [currentPhase, setCurrentPhase] = useState<'echauffement' | 'exercices' | 'retour-calme'>('echauffement')

  const today = new Date()
  const dayName = days[today.getDay()]
  const todayProgram = programs[today.getDay()]

  useEffect(() => {
    const loadDailyProgram = async () => {
      setLoading(true)
      
      // Get random exercises for today
      const exercises = getRandomExercises(4)
      setTodayExercises(exercises)

      // Fetch AI recommendations
      try {
        const [nutrition, training] = await Promise.all([
          fetchNutritionAdvice('petit-d√©jeuner √©nerg√©tique'),
          fetchTrainingTip('technique de dribble')
        ])
        setNutritionTip(nutrition)
        setTrainingTip(training)
      } catch (error) {
        console.error('Error fetching AI tips:', error)
      }

      setLoading(false)
    }

    loadDailyProgram()
  }, [])

  const warmUpExercises = [
    { name: 'Course l√©g√®re', duration: '5 min', description: 'Activation cardio-vasculaire' },
    { name: '√âtirements dynamiques', duration: '5 min', description: 'Pr√©paration articulaire' },
    { name: 'Mont√©es de genoux', duration: '2 min', description: 'Activation musculaire' }
  ]

  const coolDownExercises = [
    { name: 'Course de r√©cup√©ration', duration: '5 min', description: 'Retour au calme progressif' },
    { name: '√âtirements statiques', duration: '10 min', description: 'Relaxation musculaire' },
    { name: 'Respiration profonde', duration: '3 min', description: 'R√©cup√©ration mentale' }
  ]

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'facile': return 'bg-green-500'
      case 'moyen': return 'bg-yellow-500'
      case 'difficile': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  return (
    <div className="min-h-screen p-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Button 
            variant="ghost" 
            className="text-purple-300 hover:text-white mb-4"
            onClick={() => window.history.back()}
          >
            ‚Üê Retour
          </Button>
          <h1 className="text-3xl font-bold text-white mb-2">Entra√Ænement Quotidien</h1>
          <Card className="bg-gradient-to-r from-purple-900/40 to-pink-900/40 border-purple-500/30">
            <CardContent className="p-4">
              <h2 className="text-xl font-semibold text-white">
                {dayName} : {todayProgram}
              </h2>
            </CardContent>
          </Card>
        </div>

        {/* Phase Navigation */}
        <div className="flex gap-2 mb-6">
          {[
            { key: 'echauffement', label: '√âchauffement', icon: 'üî•' },
            { key: 'exercices', label: 'Exercices', icon: 'üí™' },
            { key: 'retour-calme', label: 'Retour au calme', icon: 'üßò' }
          ].map((phase) => (
            <Button
              key={phase.key}
              variant={currentPhase === phase.key ? "default" : "outline"}
              className={`flex-1 ${
                currentPhase === phase.key 
                  ? 'bg-purple-600 hover:bg-purple-700' 
                  : 'border-purple-500/50 text-purple-300 hover:bg-purple-900/30'
              }`}
              onClick={() => setCurrentPhase(phase.key as any)}
            >
              <span className="mr-2">{phase.icon}</span>
              {phase.label}
            </Button>
          ))}
        </div>

        {/* Content based on current phase */}
        {currentPhase === 'echauffement' && (
          <Card className="mb-6 bg-purple-900/20 border-purple-500/30">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <span>üî•</span> √âchauffement (12 min)
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {warmUpExercises.map((exercise, index) => (
                <div key={index} className="flex justify-between items-center p-3 bg-purple-800/20 rounded-lg">
                  <div>
                    <h4 className="text-white font-medium">{exercise.name}</h4>
                    <p className="text-purple-300 text-sm">{exercise.description}</p>
                  </div>
                  <Badge variant="outline" className="border-purple-400 text-purple-300">
                    {exercise.duration}
                  </Badge>
                </div>
              ))}
            </CardContent>
          </Card>
        )}

        {currentPhase === 'exercices' && (
          <div className="space-y-6">
            <Card className="bg-purple-900/20 border-purple-500/30">
              <CardHeader>
                <CardTitle className="text-white flex items-center gap-2">
                  <span>üí™</span> Exercices du jour
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {loading ? (
                  <div className="text-center py-8">
                    <div className="animate-spin w-8 h-8 border-2 border-purple-500 border-t-transparent rounded-full mx-auto mb-4"></div>
                    <p className="text-purple-300">G√©n√©ration du programme personnalis√©...</p>
                  </div>
                ) : (
                  todayExercises.map((exercise, index) => (
                    <div key={exercise.id} className="p-4 bg-purple-800/20 rounded-lg">
                      <div className="flex justify-between items-start mb-2">
                        <h4 className="text-white font-medium">{exercise.title.fr}</h4>
                        <div className="flex gap-2">
                          <Badge variant="outline" className="border-purple-400 text-purple-300">
                            {exercise.duration}
                          </Badge>
                          <Badge className={`${getDifficultyColor(exercise.difficulty)} text-white`}>
                            {exercise.difficulty}
                          </Badge>
                        </div>
                      </div>
                      <p className="text-purple-300 text-sm mb-2">{exercise.description.fr}</p>
                      <Badge variant="secondary" className="bg-purple-700/30 text-purple-200">
                        {exercise.category.replace('-', ' ')}
                      </Badge>
                    </div>
                  ))
                )}
              </CardContent>
            </Card>

            {/* AI Training Tip */}
            {trainingTip && (
              <Card className="bg-gradient-to-r from-blue-900/30 to-purple-900/30 border-blue-500/30">
                <CardHeader>
                  <CardTitle className="text-white flex items-center gap-2">
                    <span>ü§ñ</span> Conseil IA du jour
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <p className="text-blue-200">{trainingTip}</p>
                </CardContent>
              </Card>
            )}
          </div>
        )}

        {currentPhase === 'retour-calme' && (
          <Card className="mb-6 bg-purple-900/20 border-purple-500/30">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <span>üßò</span> Retour au calme (18 min)
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {coolDownExercises.map((exercise, index) => (
                <div key={index} className="flex justify-between items-center p-3 bg-purple-800/20 rounded-lg">
                  <div>
                    <h4 className="text-white font-medium">{exercise.name}</h4>
                    <p className="text-purple-300 text-sm">{exercise.description}</p>
                  </div>
                  <Badge variant="outline" className="border-purple-400 text-purple-300">
                    {exercise.duration}
                  </Badge>
                </div>
              ))}
            </CardContent>
          </Card>
        )}

        {/* Nutrition Section */}
        <Card className="bg-gradient-to-r from-green-900/30 to-purple-900/30 border-green-500/30">
          <CardHeader>
            <CardTitle className="text-white flex items-center gap-2">
              <span>ü•ó</span> Nutrition du jour
            </CardTitle>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="animate-pulse">
                <div className="h-4 bg-purple-700/30 rounded mb-2"></div>
                <div className="h-4 bg-purple-700/30 rounded w-3/4"></div>
              </div>
            ) : (
              <p className="text-green-200">{nutritionTip}</p>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
